import React from 'react';
import { Download, FileText, CheckSquare, Package, Users } from 'lucide-react';

const ExportPanel = ({
  exportConfig, 
  setExportConfig,
  handleExportMarkdown,
  handleExportAndMark,
  handleExportMarked,
  handleExportAllMarked,
  handleExportSelected,
  selectedMessageIndices
}) => {
  return (
    <div className="p-3">
      <div className="bg-gray-100 border border-gray-200 rounded">
        <div className="p-3">
          <div className="flex space-x-4">
            <div className="border border-gray-200 bg-white rounded p-2 flex-1">
              <h3 className="font-medium text-gray-700 mb-2">导出选项</h3>
              <div className="grid grid-cols-2 gap-y-1 mb-3">
                <div className="flex items-center">
                  <input 
                    type="checkbox" 
                    id="include-thinking" 
                    checked={exportConfig.includeThinking}
                    onChange={() => setExportConfig({...exportConfig, includeThinking: !exportConfig.includeThinking})}
                    className="mr-2" 
                  />
                  <label htmlFor="include-thinking" className="text-sm">包含思考过程</label>
                </div>
                <div className="flex items-center">
                  <input 
                    type="checkbox" 
                    id="include-tools" 
                    checked={exportConfig.includeTools}
                    onChange={() => setExportConfig({...exportConfig, includeTools: !exportConfig.includeTools})}
                    className="mr-2" 
                  />
                  <label htmlFor="include-tools" className="text-sm">包含工具使用</label>
                </div>
                <div className="flex items-center">
                  <input 
                    type="checkbox" 
                    id="include-citations" 
                    checked={exportConfig.includeCitations}
                    onChange={() => setExportConfig({...exportConfig, includeCitations: !exportConfig.includeCitations})}
                    className="mr-2" 
                  />
                  <label htmlFor="include-citations" className="text-sm">包含引用来源</label>
                </div>
                <div className="flex items-center">
                  <input 
                    type="checkbox" 
                    id="include-artifacts" 
                    checked={exportConfig.includeArtifacts}
                    onChange={() => setExportConfig({...exportConfig, includeArtifacts: !exportConfig.includeArtifacts})}
                    className="mr-2" 
                  />
                  <label htmlFor="include-artifacts" className="text-sm">包含Artifacts</label>
                </div>
                <div className="flex items-center">
                  <input 
                    type="checkbox" 
                    id="export-metadata" 
                    checked={exportConfig.exportObsidianMetadata}
                    onChange={() => setExportConfig({...exportConfig, exportObsidianMetadata: !exportConfig.exportObsidianMetadata})}
                    className="mr-2" 
                  />
                  <label htmlFor="export-metadata" className="text-sm">导出Obsidian元数据</label>
                </div>
                <div className="flex items-center">
                  <input 
                    type="checkbox" 
                    id="hide-timestamps" 
                    checked={exportConfig.hideTimestamps}
                    onChange={() => setExportConfig({...exportConfig, hideTimestamps: !exportConfig.hideTimestamps})}
                    className="mr-2" 
                  />
                  <label htmlFor="hide-timestamps" className="text-sm">隐藏时间戳</label>
                </div>
              </div>
            </div>
            
            <div className="border border-gray-200 bg-white rounded p-2 flex-1">
              <h3 className="font-medium text-gray-700 mb-2">导出操作</h3>
              <div className="space-y-2">
                <button 
                  className="w-full bg-blue-500 hover:bg-blue-600 text-white py-1.5 px-2 rounded text-sm flex items-center justify-center"
                  onClick={handleExportMarkdown}
                >
                  <Download className="h-3.5 w-3.5 mr-1.5" />
                  导出为Markdown
                </button>
                
                <button 
                  className="w-full bg-green-500 hover:bg-green-600 text-white py-1.5 px-2 rounded text-sm flex items-center justify-center"
                  onClick={handleExportAndMark}
                >
                  <CheckSquare className="h-3.5 w-3.5 mr-1.5" />
                  导出并标记完成
                </button>
                
                <button 
                  className="w-full bg-purple-500 hover:bg-purple-600 text-white py-1.5 px-2 rounded text-sm flex items-center justify-center"
                  onClick={handleExportSelected}
                  disabled={selectedMessageIndices.length === 0}
                >
                  <Users className="h-3.5 w-3.5 mr-1.5" />
                  导出选中消息
                </button>
              </div>
            </div>
          </div>
          
          <div className="flex space-x-4 mt-3">
            <div className="border border-gray-200 bg-white rounded p-2 flex-1">
              <h3 className="font-medium text-gray-700 mb-2">批量导出</h3>
              <div className="flex space-x-2">
                <button 
                  className="flex-1 bg-teal-500 hover:bg-teal-600 text-white py-1.5 px-2 rounded text-sm flex items-center justify-center"
                  onClick={handleExportMarked}
                >
                  <FileText className="h-3.5 w-3.5 mr-1.5" />
                  仅导出已标记内容
                </button>
                
                <button 
                  className="flex-1 bg-indigo-500 hover:bg-indigo-600 text-white py-1.5 px-2 rounded text-sm flex items-center justify-center"
                  onClick={handleExportAllMarked}
                >
                  <Package className="h-3.5 w-3.5 mr-1.5" />
                  批量导出所有已标记
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ExportPanel;