import React, { useRef } from 'react';
import { Search } from 'lucide-react';
import ReactMarkdown from 'react-markdown';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { vscDarkPlus } from 'react-syntax-highlighter/dist/esm/styles/prism';
import remarkGfm from 'remark-gfm';

// 自定义渲染器，增强标题渲染
const customRenderers = {
  h2: ({node, children}) => {
    return (
      <h2 className="text-xl font-bold text-blue-700 border-b border-blue-200 pb-1 mt-5 mb-3">
        {children}
      </h2>
    );
  },
  h3: ({node, children}) => {
    return (
      <h3 className="text-lg font-semibold text-blue-600 mt-4 mb-2">
        {children}
      </h3>
    );
  }
};

// 添加一些CSS样式到<head>
if (typeof document !== 'undefined') {
  const style = document.createElement('style');
  style.innerHTML = `
    .markdown-content h2 {
      font-size: 1.5rem;
      font-weight: bold;
      color: #1d4ed8;
      border-bottom: 1px solid #dbeafe;
      padding-bottom: 0.25rem;
      margin-top: 1.25rem;
      margin-bottom: 0.75rem;
    }
    .markdown-content h3 {
      font-size: 1.25rem;
      font-weight: 600;
      color: #2563eb;
      margin-top: 1rem;
      margin-bottom: 0.5rem;
    }
    .markdown-content ul {
      list-style-type: disc;
      padding-left: 1.5rem;
      margin: 0.5rem 0;
    }
    .markdown-content ol {
      list-style-type: decimal;
      padding-left: 1.5rem;
      margin: 0.5rem 0;
    }
    .markdown-content a {
      color: #3b82f6;
      text-decoration: underline;
    }
    .markdown-content blockquote {
      border-left: 4px solid #e5e7eb;
      padding-left: 1rem;
      font-style: italic;
      margin: 0.5rem 0;
    }
    .markdown-content table {
      border-collapse: collapse;
      width: 100%;
      margin: 1rem 0;
    }
    .markdown-content table th,
    .markdown-content table td {
      border: 1px solid #e5e7eb;
      padding: 0.5rem;
    }
    .markdown-content table th {
      background-color: #f9fafb;
      font-weight: bold;
    }
    
    /* 修复JSON选项卡中的布局问题 */
    .json-content {
      max-width: 100%;
      overflow-x: auto;
    }
    
    .detail-content-container {
      width: 100%;
      height: 100%;
      overflow: auto;
    }
    
    /* 限制JSON代码块的宽度 */
    pre[class*="language-json"] {
      max-width: 100% !important;
      white-space: pre-wrap !important;
      word-break: break-word !important;
    }
    
    /* 确保包含JSON的容器不会挤压其他元素 */
    .json-tab-content {
      max-width: 100%;
      overflow-x: auto !important;
      overflow-y: auto !important;
    }
  `;
  document.head.appendChild(style);
}

const MessageDetail = ({
  processedData,
  selectedMessageIndices,
  activeTab,
  setActiveTab,
  contentSearchQuery,
  setContentSearchQuery,
  handleSearchInContent,
}) => {
  const contentTextRef = useRef(null);
  const thinkingTextRef = useRef(null);
  const artifactsTextRef = useRef(null);
  const jsonTextRef = useRef(null);
  
  // 渲染消息详情
  const renderMessageDetails = () => {
    if (selectedMessageIndices.length === 0 || !processedData) {
      return <div className="p-4 text-gray-500">请选择一条消息</div>;
    }

    // 多选与单选逻辑调整
    if (selectedMessageIndices.length > 1) {
      // 多选显示合并视图
      const selectedMessages = selectedMessageIndices
        .map(index => processedData.chat_history.find(msg => msg.index === index))
        .filter(Boolean);

      const humanCount = selectedMessages.filter(msg => msg.sender === 'human').length;
      const claudeCount = selectedMessages.filter(msg => msg.sender === 'assistant').length;

      return (
        <div className="p-4 detail-content-container">
          <div className="font-medium text-green-600 mb-4">
            已选择 {selectedMessages.length} 条消息 (人类: {humanCount}, Claude: {claudeCount})
          </div>
          
          {activeTab === 'content' && (
            <div>
              <div className="font-medium mb-2">选中消息内容:</div>
              
              {selectedMessages.map((msg, idx) => (
                <div key={idx} className="mb-4 pb-4 border-b border-gray-200">
                  <div className="font-medium">
                    {idx + 1}. {msg.sender_label} @ {msg.timestamp}
                  </div>
                  {msg.sender === 'assistant' && msg.tools && msg.tools.length > 0 && (
                    <div className="text-blue-600 text-sm mb-1">
                      [使用了 {msg.tools.length} 个工具: {msg.tools.map(t => t.name).join(', ')}]
                    </div>
                  )}
                  {msg.sender === 'assistant' && msg.artifacts && msg.artifacts.length > 0 && (
                    <div className="text-purple-600 text-sm mb-1">
                      [创建了 {msg.artifacts.length} 个Artifacts]
                    </div>
                  )}
                  <div className="mt-1">
                    <ReactMarkdown
                      remarkPlugins={[remarkGfm]}
                      components={{
                        code({node, inline, className, children, ...props}) {
                          const match = /language-(\w+)/.exec(className || '')
                          return !inline && match ? (
                            <SyntaxHighlighter
                              style={vscDarkPlus}
                              language={match[1]}
                              PreTag="div"
                              {...props}
                            >{String(children).replace(/\n$/, '')}</SyntaxHighlighter>
                          ) : (
                            <code className={className} {...props}>
                              {children}
                            </code>
                          )
                        }
                      }}
                    >
                      {msg.display_text}
                    </ReactMarkdown>
                  </div>
                </div>
              ))}
            </div>
          )}
          
          {activeTab === 'thinking' && (
            <div>
              <div className="font-medium mb-2">思考过程:</div>
              
              {selectedMessages
                .filter(msg => msg.thinking)
                .map((msg, idx) => (
                  <div key={idx} className="mb-4 pb-4 border-b border-gray-200">
                    <div className="font-medium">
                      {msg.sender_label} @ {msg.timestamp}
                    </div>
                    <pre className="whitespace-pre-wrap font-mono text-sm mt-2 bg-gray-50 p-3 rounded">{msg.thinking}</pre>
                  </div>
                ))}
                
              {selectedMessages.filter(msg => msg.thinking).length === 0 && (
                <div className="text-gray-500">所选消息没有包含思考过程。</div>
              )}
            </div>
          )}
          
          {activeTab === 'artifacts' && (
            <div>
              <div className="font-medium mb-2">Artifacts:</div>
              
              {selectedMessages
                .filter(msg => msg.artifacts && msg.artifacts.length > 0)
                .map((msg, msgIdx) => (
                  <div key={msgIdx} className="mb-4">
                    <div className="font-medium border-b border-gray-200 pb-1 mb-2">
                      {msg.sender_label} @ {msg.timestamp}:
                    </div>
                    
                    {msg.artifacts.map((artifact, artIdx) => (
                      <div key={artIdx} className="mb-4 pb-4 border-b border-gray-200 pl-4">
                        <div className="font-medium">
                          Artifact {artIdx + 1}: {artifact.title || '无标题'} (ID: {artifact.id || '未知'})
                        </div>
                        <div className="text-sm mt-1">类型: {artifact.type || '未知'}</div>
                        <div className="border-t border-gray-200 my-2"></div>
                        
                        {artifact.command === 'create' && (
                          <>
                            {artifact.language && <div className="text-sm">语言: {artifact.language}</div>}
                            <div className="mt-2">内容:</div>
                            <SyntaxHighlighter
                              style={vscDarkPlus}
                              language={artifact.language || 'text'}
                              className="text-xs mt-1 rounded"
                            >
                              {artifact.content || ''}
                            </SyntaxHighlighter>
                          </>
                        )}
                        
                        {(artifact.command === 'update' || artifact.command === 'rewrite') && (
                          <>
                            <div className="text-sm">操作: {artifact.command}</div>
                            <div className="mt-2">原始文本:</div>
                            <pre className="bg-gray-50 p-2 rounded mt-1 text-xs overflow-auto">
                              {artifact.old_str || ''}
                            </pre>
                            <div className="mt-2">新文本:</div>
                            <pre className="bg-gray-50 p-2 rounded mt-1 text-xs overflow-auto">
                              {artifact.new_str || ''}
                            </pre>
                          </>
                        )}
                      </div>
                    ))}
                  </div>
                ))}
                
              {selectedMessages.filter(msg => msg.artifacts && msg.artifacts.length > 0).length === 0 && (
                <div className="text-gray-500">所选消息没有包含Artifacts。</div>
              )}
            </div>
          )}
          
          {activeTab === 'json' && (
            <div className="json-tab-content">
              <div className="font-medium mb-2">原始JSON:</div>
              
              {selectedMessages.map((msg, idx) => (
                <div key={idx} className="mb-4 pb-4 border-b border-gray-200">
                  <div className="font-medium">
                    {msg.sender_label} @ {msg.timestamp}:
                  </div>
                  <div className="json-content">
                    <SyntaxHighlighter
                      style={vscDarkPlus}
                      language="json"
                      className="text-xs mt-2 rounded"
                      wrapLines={true}
                      wrapLongLines={true}
                    >
                      {JSON.stringify(msg.content_items || {}, null, 2)}
                    </SyntaxHighlighter>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>
      );
    }

    // 单选显示
    const messageIndex = selectedMessageIndices[0];
    const message = processedData.chat_history.find(msg => msg.index === messageIndex);
    
    if (!message) return <div className="p-4 text-gray-500">消息不存在</div>;
    
    if (activeTab === 'content') {
      return (
        <div className="p-4 detail-content-container" ref={contentTextRef}>
          {message.sender === 'assistant' && message.tools && message.tools.length > 0 && (
            <div className="text-blue-600 mb-3">
              [使用了 {message.tools.length} 个工具: {message.tools.map(t => t.name).join(', ')}]
              
              {message.tools.map((tool, idx) => (
                <div key={idx} className="ml-4 mt-2 mb-3 text-sm">
                  <div className="font-medium">工具 {idx + 1}: {tool.name}</div>
                  <div className="border-t border-gray-200 my-1"></div>
                  
                  {tool.input && typeof tool.input === 'object' && (
                    <div className="mt-1">
                      <div>输入参数:</div>
                      <SyntaxHighlighter
                        style={vscDarkPlus}
                        language="json"
                        className="text-xs mt-1 rounded"
                      >
                        {JSON.stringify(tool.input, null, 2)}
                      </SyntaxHighlighter>
                    </div>
                  )}
                  
                  {tool.result && (
                    <div className="mt-2">
                      <div>结果状态: {tool.result.is_error ? '出错' : '成功'}</div>
                      
                      {tool.name === 'web_search' && tool.result.content && (
                        <div className="mt-1">
                          <div>搜索结果:</div>
                          {tool.result.content.slice(0, 5).map((result, i) => (
                            <div key={i} className="ml-2 mt-1">
                              {i + 1}. {result.title}
                              <div className="text-xs text-blue-500">{result.url}</div>
                            </div>
                          ))}
                          {tool.result.content.length > 5 && (
                            <div className="text-xs mt-1">...还有 {tool.result.content.length - 5} 个结果未显示</div>
                          )}
                        </div>
                      )}
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
          
          {message.sender === 'assistant' && message.artifacts && message.artifacts.length > 0 && (
            <div className="text-purple-600 mb-3">
              [创建了 {message.artifacts.length} 个Artifacts]
            </div>
          )}
          
          <ReactMarkdown
            remarkPlugins={[remarkGfm]}
            components={{
              code({node, inline, className, children, ...props}) {
                const match = /language-(\w+)/.exec(className || '')
                return !inline && match ? (
                  <SyntaxHighlighter
                    style={vscDarkPlus}
                    language={match[1]}
                    PreTag="div"
                    {...props}
                  >{String(children).replace(/\n$/, '')}</SyntaxHighlighter>
                ) : (
                  <code className={className} {...props}>
                    {children}
                  </code>
                )
              },
              ...customRenderers
            }}
          >
            {message.display_text}
          </ReactMarkdown>
        </div>
      );
    } else if (activeTab === 'thinking') {
      return (
        <div className="p-4 detail-content-container" ref={thinkingTextRef}>
          {message.thinking ? (
            <pre className="whitespace-pre-wrap font-mono text-sm">{message.thinking}</pre>
          ) : (
            <div className="text-gray-500">此消息没有思考过程记录。</div>
          )}
        </div>
      );
    } else if (activeTab === 'artifacts') {
      return (
        <div className="p-4 detail-content-container" ref={artifactsTextRef}>
          {message.artifacts && message.artifacts.length > 0 ? (
            message.artifacts.map((artifact, idx) => (
              <div key={idx} className="mb-4 pb-4 border-b border-gray-200">
                <div className="font-medium">
                  Artifact {idx + 1}: {artifact.title || '无标题'} (ID: {artifact.id || '未知'})
                </div>
                <div className="text-sm mt-1">类型: {artifact.type || '未知'}</div>
                <div className="border-t border-gray-200 my-2"></div>
                
                {artifact.command === 'create' && (
                  <>
                    {artifact.language && <div className="text-sm">语言: {artifact.language}</div>}
                    <div className="mt-2">内容:</div>
                    <SyntaxHighlighter
                      style={vscDarkPlus}
                      language={artifact.language || 'text'}
                      className="text-xs mt-1 rounded"
                    >
                      {artifact.content || ''}
                    </SyntaxHighlighter>
                  </>
                )}
                
                {(artifact.command === 'update' || artifact.command === 'rewrite') && (
                  <>
                    <div className="text-sm">操作: {artifact.command}</div>
                    <div className="mt-2">原始文本:</div>
                    <SyntaxHighlighter
                      style={vscDarkPlus}
                      language="text"
                      className="text-xs mt-1 rounded"
                    >
                      {artifact.old_str || ''}
                    </SyntaxHighlighter>
                    <div className="mt-2">新文本:</div>
                    <SyntaxHighlighter
                      style={vscDarkPlus}
                      language="text"
                      className="text-xs mt-1 rounded"
                    >
                      {artifact.new_str || ''}
                    </SyntaxHighlighter>
                  </>
                )}
                
                {artifact.result && (
                  <div className="mt-2">
                    结果状态: {artifact.result.is_error ? '出错' : '成功'}
                  </div>
                )}
              </div>
            ))
          ) : (
            <div className="text-gray-500">此消息没有使用Artifacts。</div>
          )}
        </div>
      );
    } else if (activeTab === 'json') {
      return (
        <div className="p-4 detail-content-container json-tab-content" ref={jsonTextRef}>
          <div className="json-content">
            <SyntaxHighlighter
              style={vscDarkPlus}
              language="json"
              className="text-xs rounded"
              wrapLines={true}
              wrapLongLines={true}
            >
              {JSON.stringify(message.content_items || {}, null, 2)}
            </SyntaxHighlighter>
          </div>
        </div>
      );
    }
    
    return null;
  };

  return (
    <div className="flex-1 flex flex-col h-full overflow-hidden">
      {/* 消息详情 */}
      <div className="flex-1 flex flex-col">
        {/* 选项卡 */}
        <div className="bg-white border-b border-gray-300 flex">
          <button 
            className={`px-4 py-2 text-sm font-medium ${
              activeTab === 'content' 
                ? 'text-blue-600 border-b-2 border-blue-500' 
                : 'text-gray-500 hover:text-gray-700'
            }`}
            onClick={() => setActiveTab('content')}
          >
            内容
          </button>
          <button 
            className={`px-4 py-2 text-sm font-medium ${
              activeTab === 'thinking' 
                ? 'text-blue-600 border-b-2 border-blue-500' 
                : 'text-gray-500 hover:text-gray-700'
            }`}
            onClick={() => setActiveTab('thinking')}
          >
            思考过程
          </button>
          <button 
            className={`px-4 py-2 text-sm font-medium ${
              activeTab === 'artifacts' 
                ? 'text-blue-600 border-b-2 border-blue-500' 
                : 'text-gray-500 hover:text-gray-700'
            }`}
            onClick={() => setActiveTab('artifacts')}
          >
            Artifacts
          </button>
          <button 
            className={`px-4 py-2 text-sm font-medium ${
              activeTab === 'json' 
                ? 'text-blue-600 border-b-2 border-blue-500' 
                : 'text-gray-500 hover:text-gray-700'
            }`}
            onClick={() => setActiveTab('json')}
          >
            原始JSON
          </button>
        </div>

        {/* 内容搜索 */}
        <div className="p-2 bg-white border-b border-gray-300 flex items-center">
          <div className="relative flex-1">
            <input 
              type="text" 
              placeholder="在内容中查找..." 
              className="w-full pl-8 pr-2 py-1 border border-gray-300 rounded text-sm"
              value={contentSearchQuery}
              onChange={(e) => setContentSearchQuery(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSearchInContent('next')}
            />
            <Search className="absolute left-2 top-1.5 h-4 w-4 text-gray-400" />
          </div>
          <button 
            className="ml-2 bg-gray-200 py-1 px-2 rounded text-sm whitespace-nowrap"
            onClick={() => handleSearchInContent('next')}
          >
            下一个
          </button>
          <button 
            className="ml-2 bg-gray-200 py-1 px-2 rounded text-sm whitespace-nowrap"
            onClick={() => handleSearchInContent('prev')}
          >
            上一个
          </button>
        </div>

        {/* 内容区域 */}
        <div className="flex-1 overflow-hidden bg-white h-screen max-h-[70vh]">
          {renderMessageDetails()}
        </div>
      </div>
    </div>
  );
};

export default MessageDetail;