import React from 'react';
import { Search, ChevronLeft, ChevronRight, CheckSquare, FileText, Upload, Trash2, PlusCircle } from 'lucide-react';
import BranchManager from './BranchManager';

const MessageList = ({ 
  processedData, 
  selectedMessageIndices, 
  handleMessageSelect, 
  handleToggleCompleted,
  files,
  currentFileIndex,
  handlePrevFile,
  handleNextFile,
  handleRemoveCurrentFile,
  searchQuery,
  setSearchQuery,
  handleSearchMessages,
  markedItems,
  importantItems,
  deletedItems,
  showCompleted,
  showDeleted,
  selectedBranchIndices,
  handleSelectMultipleBranches,
  handleMarkBranchCompleted,
  handleExportSelectedBranch,
  handleDeleteBranch,
  handleLoadClick,
  handleMarkDeleted
}) => {
  // 计算选中分支包含多少消息
  const calculateSelectedMessageCount = () => {
    if (selectedBranchIndices.length === 0 || !processedData || !processedData.branches) {
      return 0;
    }
    
    // 获取所有选中分支的消息索引
    const uniqueMessages = new Set();
    selectedBranchIndices.forEach(branchIdx => {
      const branch = processedData.branches[branchIdx];
      branch.forEach(msg => uniqueMessages.add(msg.index));
    });
    
    return uniqueMessages.size;
  };

  return (
    <div className="w-2/5 max-w-lg flex flex-col border-r border-gray-300 message-list-panel">
      {/* 顶部按钮区 */}
      <div className="p-2 border-b border-gray-300 bg-white">
        <div className="flex space-x-2 items-center">
          <div className="relative flex-1">
            <input 
              type="text" 
              placeholder="搜索..." 
              className="w-full pl-8 pr-2 py-1 border border-gray-300 rounded text-sm"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              onKeyPress={(e) => e.key === 'Enter' && handleSearchMessages('next')}
            />
            <Search className="absolute left-2 top-1.5 h-4 w-4 text-gray-400" />
          </div>
          <button 
            className="bg-gray-200 py-1 px-2 rounded text-sm whitespace-nowrap"
            onClick={() => handleSearchMessages('next')}
          >
            下一个
          </button>
          <button 
            className="bg-gray-200 py-1 px-2 rounded text-sm whitespace-nowrap"
            onClick={() => handleSearchMessages('prev')}
          >
            上一个
          </button>
        </div>
      </div>
      
      {/* 对话标题和文件操作 - 放在分支管理之前 */}
      <div className="p-3 bg-blue-50 border-b border-gray-300">
        <h2 className="font-bold text-lg text-center text-blue-800">
          {processedData?.meta_info?.title || '无标题对话'}
        </h2>
        <div className="flex justify-between text-sm text-gray-600 mt-1">
          <div className="flex items-center">
            <span>
              {processedData?.chat_history?.length || 0}条消息
              {processedData?.branches?.length > 0 && ` | ${processedData.branches.length}个分支`}
            </span>
            
            {/* 添加文件操作按钮 */}
            <div className="ml-3 flex space-x-1">
              <button
                className="text-blue-600 hover:text-blue-800 focus:outline-none"
                onClick={handleLoadClick}
                title="添加新文件"
              >
                <PlusCircle className="h-4 w-4" />
              </button>
              <button
                className="text-red-600 hover:text-red-800 focus:outline-none"
                onClick={handleRemoveCurrentFile}
                title="移除当前文件"
              >
                <Trash2 className="h-4 w-4" />
              </button>
            </div>
          </div>
          
          {files.length > 1 && (
            <div className="flex items-center space-x-1">
              <button 
                className="border rounded px-1 text-gray-500"
                disabled={currentFileIndex === 0}
                onClick={handlePrevFile}
              >
                <ChevronLeft className="h-4 w-4" />
              </button>
              <span>{currentFileIndex + 1}/{files.length}</span>
              <button 
                className="border rounded px-1 text-gray-500"
                disabled={currentFileIndex === files.length - 1}
                onClick={handleNextFile}
              >
                <ChevronRight className="h-4 w-4" />
              </button>
            </div>
          )}
        </div>
      </div>

      {/* 消息列表 - 调整顺序到分支管理之前 */}
      <div className="flex-1 overflow-y-auto h-screen max-h-[60vh]">
        <div className="bg-gray-100 border-b border-gray-300 p-1 sticky top-0 z-10">
          <div className="grid grid-cols-12 text-sm font-medium text-gray-600">
            <div className="col-span-1 px-1">序号</div>
            <div className="col-span-2 px-1">发送者</div>
            <div className="col-span-3 px-1">时间</div>
            <div className="col-span-1 px-1">分支</div>
            <div className="col-span-1 px-1">标记</div>
            <div className="col-span-4 px-1 flex justify-end pr-2">
              <button
                className="text-gray-500 hover:text-red-600 focus:outline-none"
                onClick={handleMarkDeleted}
                disabled={selectedMessageIndices.length === 0}
                title="移除选中消息"
              >
                <Trash2 className="h-4 w-4" />
              </button>
            </div>
          </div>
        </div>
        
        {processedData?.chat_history?.map((msg) => {
          // 如果消息被标记为已删除且设置为隐藏已删除，则跳过
          if (deletedItems.has(msg.index) && !showDeleted) {
            return null;
          }
          
          // 如果消息被标记为已完成且设置为隐藏已完成，则跳过
          if (markedItems.has(msg.index) && !showCompleted) {
            return null;
          }
          
          // 高亮显示选中分支的消息
          let isInSelectedBranch = false;
          if (selectedBranchIndices.length > 0 && processedData.branches) {
            selectedBranchIndices.forEach(branchIdx => {
              if (processedData.branches[branchIdx] && 
                  processedData.branches[branchIdx].some(m => m.index === msg.index)) {
                isInSelectedBranch = true;
              }
            });
          }
          
          // 生成分支标记
          let branchInfo = "";
          if (msg.is_branch_point) {
            branchInfo = "🔀";
          } else if (msg.branch_level > 0) {
            branchInfo = `${msg.branch_level}`;
          }
          
          // 生成标记信息
          let markInfo = null;
          
          if (deletedItems.has(msg.index)) {
            markInfo = <span className="deleted-mark" style={{color: selectedMessageIndices.includes(msg.index) ? '#ffcdd2' : '#f44336'}}>🗑️</span>;
          } else if (markedItems.has(msg.index) && importantItems.has(msg.index)) {
            markInfo = (
              <div className="dual-mark" style={{color: selectedMessageIndices.includes(msg.index) ? 'white' : 'inherit'}}>
                <span className="completed-mark" style={{color: selectedMessageIndices.includes(msg.index) ? '#a5d6a7' : '#4caf50'}}>✓</span>
                <span className="important-mark" style={{color: selectedMessageIndices.includes(msg.index) ? '#ffe082' : '#ff8f00'}}>⭐</span>
              </div>
            );
          } else if (markedItems.has(msg.index)) {
            markInfo = <span className="completed-mark" style={{color: selectedMessageIndices.includes(msg.index) ? '#a5d6a7' : '#4caf50'}}>✓</span>;
          } else if (importantItems.has(msg.index)) {
            markInfo = <span className="important-mark" style={{color: selectedMessageIndices.includes(msg.index) ? '#ffe082' : '#ff8f00'}}>⭐</span>;
          }

          // 已删除消息的样式
          const isDeleted = deletedItems.has(msg.index);
          const deletedStyle = isDeleted ? { textDecoration: 'line-through', opacity: 0.6 } : {};

          const grid = "grid grid-cols-12 px-1 py-3 text-sm";
          const messagePreviewClass = selectedMessageIndices.includes(msg.index) 
            ? "col-span-4 px-1 pl-3 truncate text-white" 
            : "col-span-4 px-1 pl-3 truncate text-gray-700";
          
          return (
            <div 
              key={msg.index} 
              className={`border-b border-gray-200 hover:bg-gray-100 ${
                msg.sender === 'human' ? 'human-msg' : 'claude-msg'
              } message-list-item cursor-pointer ${isInSelectedBranch ? 'border-l-4 border-l-blue-500' : ''}`}
              onClick={(e) => handleMessageSelect(msg.index, e.ctrlKey)}
              onDoubleClick={() => handleToggleCompleted(msg.index)}
              style={{
                backgroundColor: selectedMessageIndices.includes(msg.index) ? '#5c6bc0' : '',
                color: selectedMessageIndices.includes(msg.index) ? 'white' : '',
                ...deletedStyle
              }}
            >
              <div className={grid}>
                <div className="col-span-1 px-1 text-center">{msg.index + 1}</div>
                <div className="col-span-2 px-1 font-medium">
                  {msg.sender_label}
                </div>
                <div className="col-span-3 px-1 text-xs" style={{color: selectedMessageIndices.includes(msg.index) ? '#e0e0e0' : '#6b7280'}}>
                  {msg.timestamp}
                </div>
                <div className="col-span-1 px-1 text-center" style={{color: selectedMessageIndices.includes(msg.index) ? '#e0e0e0' : '#9c27b0'}}>
                  {branchInfo}
                </div>
                <div className="col-span-1 px-1 text-center mark-container">
                  {markInfo}
                </div>
                <div className={messagePreviewClass}>{msg.display_text.substring(0, 100)}</div>
              </div>
            </div>
          );
        })}
      </div>
      
      {/* 使用新的分支管理器组件 */}
      <BranchManager
        processedData={processedData}
        selectedBranchIndices={selectedBranchIndices}
        handleSelectBranch={handleSelectMultipleBranches}
        handleMarkBranchCompleted={handleMarkBranchCompleted}
        handleExportSelectedBranch={handleExportSelectedBranch}
        handleDeleteBranch={handleDeleteBranch}
      />
    </div>
  );
};

export default MessageList;