import React from 'react';
import { CheckSquare, Star, Clipboard, RotateCcw, RotateCw, Trash2 } from 'lucide-react';

const ToolActionPanel = ({
  showCompleted,
  setShowCompleted,
  showDeleted,
  setShowDeleted,
  handleMarkCompleted,
  handleMarkImportant,
  handleMarkDeleted,
  handleClearMarks,
  handleCopySelected,
  handleUndo,
  handleRedo,
  undoStack,
  redoStack,
  selectedMessageIndices
}) => {
  return (
    <div className="p-3 flex space-x-4">
      <div className="border border-gray-200 bg-white rounded p-2 flex-1">
        <h3 className="font-medium text-gray-700 mb-2">标记控制</h3>
        <div className="flex space-y-1 flex-col mb-2">
          <div className="flex items-center">
            <input 
              type="checkbox" 
              id="show-completed" 
              checked={showCompleted}
              onChange={() => setShowCompleted(!showCompleted)}
              className="mr-2" 
            />
            <label htmlFor="show-completed" className="text-sm">显示已完成标记</label>
          </div>
          <div className="flex items-center">
            <input 
              type="checkbox" 
              id="show-deleted" 
              checked={showDeleted}
              onChange={() => setShowDeleted(!showDeleted)}
              className="mr-2" 
            />
            <label htmlFor="show-deleted" className="text-sm">显示已移除消息</label>
          </div>
        </div>
        <div className="flex space-x-2">
          <button 
            className="bg-green-100 text-green-700 py-1 px-2 rounded text-xs flex items-center"
            onClick={handleMarkCompleted}
            disabled={selectedMessageIndices.length === 0}
          >
            <CheckSquare className="h-3 w-3 mr-1" />
            标记为已完成
          </button>
          <button 
            className="bg-yellow-100 text-yellow-700 py-1 px-2 rounded text-xs flex items-center"
            onClick={handleMarkImportant}
            disabled={selectedMessageIndices.length === 0}
          >
            <Star className="h-3 w-3 mr-1" />
            标记为重点
          </button>
          <button 
            className="bg-red-100 text-red-700 py-1 px-2 rounded text-xs flex items-center"
            onClick={handleMarkDeleted}
            disabled={selectedMessageIndices.length === 0}
          >
            <Trash2 className="h-3 w-3 mr-1" />
            移除消息
          </button>
          <button 
            className="bg-gray-100 text-gray-700 py-1 px-2 rounded text-xs"
            onClick={handleClearMarks}
            disabled={selectedMessageIndices.length === 0}
          >
            清除标记
          </button>
        </div>
      </div>
      
      <div className="border border-gray-200 bg-white rounded p-2 flex-1">
        <h3 className="font-medium text-gray-700 mb-2">常用操作</h3>
        <div className="flex space-x-2">
          <button 
            className="bg-blue-100 text-blue-700 py-1 px-2 rounded text-xs flex items-center"
            onClick={handleCopySelected}
            disabled={selectedMessageIndices.length === 0}
          >
            <Clipboard className="h-3 w-3 mr-1" />
            复制选中内容
          </button>
        </div>
        <div className="flex space-x-2 mt-2">
          <button 
            className="bg-gray-200 text-gray-700 py-1 px-2 rounded text-xs flex items-center"
            onClick={handleUndo}
            disabled={undoStack.length === 0}
          >
            <RotateCcw className="h-3 w-3 mr-1" />
            撤销
          </button>
          <button 
            className="bg-gray-200 text-gray-700 py-1 px-2 rounded text-xs flex items-center"
            onClick={handleRedo}
            disabled={redoStack.length === 0}
          >
            <RotateCw className="h-3 w-3 mr-1" />
            重做
          </button>
        </div>
      </div>
    </div>
  );
};

export default ToolActionPanel;